import Link from "next/link";
import type { THeader } from "@/types";

import { Logo } from "@/components/custom/logo";
import { Navigation } from "@/components/custom/navigation";
import { Button } from "@/components/ui/button";


interface IHeaderProps {
  data?: THeader | null;
}

export async function Header({ data }: IHeaderProps) {
  if (!data) return null;

  const { logoText, ctaButton } = data;
  return (
    <div className="relative flex flex-col items-center px-4 py-4 bg-black text-white shadow-md dark:bg-gray-800">
      <Link href={ctaButton.href} className="absolute top-1/2 -translate-y-1/2 right-4">
        <Button className="bg-brand-pink text-brand-red font-sans font-bold text-md italic px-4 py-3 hover:bg-brand-red/90">
          {ctaButton.label}
        </Button>
      </Link>
      <Logo data={logoText} />
      <div className="flex items-center gap-4 mt-4">
        <Navigation />
      </div>
    </div>
  );
}